<template>
  <mu-card>
      <mu-card-header :title="user.name" subTitle="Try to make changes.">
        <mu-avatar src="https://shaodushu.cn/pic/admin/user_img.jpg" slot="avatar" :size="60"/>
      </mu-card-header>
      <mu-divider />
      <mu-list>
        <mu-list-item title="钱包" :describeText="user.money+''">
          <mu-avatar icon="account_balance_wallet" backgroundColor="#ed3f14" slot="leftAvatar" />
        </mu-list-item>
        <mu-list-item title="加入时间" :describeText="user.creatTime | format">
          <mu-avatar icon="redeem" backgroundColor="#ff9900" slot="leftAvatar" />
        </mu-list-item>
      </mu-list>
    </mu-card>
</template>
<script>
import { formatDate } from "@/common/js/date";
import Cookies from "js-cookie";
export default {
  computed: {
    user() {
      return this.$store.state.user;
    }
  },
  filters: {
    format(time) {
      let date = new Date(time);
      return formatDate(date, "yyyy-MM-dd hh:mm");
    }
  }
};
</script>
<style lang="scss" scoped>
.mu-card {
  height: 100%;
  .mu-card-header {
    display: flex;
  }
}
</style>



